"use client";
import { useState } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { BarChart2, ShoppingBag, Users, DollarSign, Settings, LogOut } from "lucide-react";
import { Montserrat } from "next/font/google";

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const montserrat = Montserrat({
    subsets: ["latin"],
    weight: "800",
    display: "swap",
  });
  
  const pathname = usePathname();
  const [isCollapsed, setIsCollapsed] = useState(false);
  
  const navItems = [
    { name: "Tableau de bord", href: "/admin/dashboard", icon: BarChart2 },
    { name: "Produits", href: "/admin/products", icon: ShoppingBag },
    { name: "Utilisateurs", href: "/admin/users", icon: Users },
    { name: "Ventes", href: "/admin/sales", icon: DollarSign },
    { name: "Paramètres", href: "/admin/settings", icon: Settings },
  ];
  
  return (
    <>
      <div
        className={`fixed top-0 left-0 h-screen ${isCollapsed ? "w-20" : "w-64"} bg-gray-100 transition-all duration-300 flex flex-col shadow-lg z-50`}
      >
        {/* Logo ou titre */}
        <div
          className={`p-4 flex items-center gap-2 ${
            isCollapsed ? "justify-center" : "justify-start"
          }`}
        >
          <BarChart2 className="h-8 w-8 text-gray-900" />
          {!isCollapsed && (
            <h2
              className={`${montserrat.className} text-xl font-bold text-gray-900`}
            >
              Admin
            </h2>
          )}
        </div>

        {/* Navigation */}
        <nav className="flex-1 mt-6">
          {navItems.map((item) => {
            const isActive = pathname === item.href;
            return (
              <Link
                key={item.name}
                href={item.href}
                className={`flex items-center gap-4 py-3 px-6 ${
                  isActive
                    ? "bg-gray-200 text-gray-900"
                    : "text-gray-700 hover:bg-gray-200"
                } transition-all duration-200`}
              >
                <item.icon
                  className={`h-6 w-6 ${isActive ? "text-teal-400" : ""}`}
                />
                {!isCollapsed && <span>{item.name}</span>}
              </Link>
            );
          })}
        </nav>

        {/* Bouton de déconnexion */}
        <div className="p-4">
          <button
            className="flex items-center gap-4 py-3 px-6 w-full text-gray-700 hover:bg-gray-200 transition-all duration-200"
          >
            <LogOut className="h-6 w-6" />
            {!isCollapsed && <span>Déconnexion</span>}
          </button>
        </div>

        {/* Bouton pour collapse/expand */}
        <button
          onClick={() => setIsCollapsed(!isCollapsed)}
          className="absolute top-4 right-[-12px] p-1 rounded-full border-2 bg-gray-200 text-gray-900 border-gray-300"
        >
          {isCollapsed ? ">" : "<"}
        </button>
      </div>
      <section>{children}</section>
    </>
  );
}